*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
:root{
    --black-color: black;
    --light-font:  #EADFE8;
    --grey-font:   #a6a6a6;
    --purple: #926A8E;
}

.link-container{
    position:fixed;
    right: 1%;
    bottom: 0;
    
    width: 8%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
.imp-links{
    width: 40%;
    height: 20%;
    
}
.twitter-link{
    background-image: url("../utils/img/twitter-icon.png");
    background-repeat: no-repeat;
    background-size: contain;
    transition: background-image 0.4s ease;
}
.twitter-link:hover{
    cursor: pointer;
    background-image: url("../utils/img/twitter-icon-purple.png");
}
.instagram-link{
    background-image: url("../utils/img/instagram-icon.png");
    background-repeat: no-repeat;
    background-size: contain;
    transition: background-image 0.4s ease;
}
.instagram-link:hover{
    cursor: pointer;
    background-image: url("../utils/img/instagram-icon-purple.png");
}
.linked-link{
    background-image: url("../utils/img/linkedin-icons.png");
    background-repeat: no-repeat;
    background-size: contain;
    transition: background-image 0.4s ease;
}
.linked-link:hover{
    cursor: pointer;
    background-image: url("../utils/img/linkedin-icons-purple.png");
}
.vertical-line{
    border-radius: 4em;
    height: 80%;
    width: 1.4px;
  
 
    background-color: #926A8E;
}