*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: rgb(20, 15, 22);
    --light-font:  #EADFE8;
    --purple: #926A8E;
    --light-orange:rgb(255, 223, 143);
    --green:rgb(74, 214, 165);
    --step--2: clamp(0.78rem, calc(0.66rem + 0.59vw), 1.12rem);
    --step--1: clamp(0.94rem, calc(0.78rem + 0.80vw), 1.40rem);
    --step-0: clamp(1.13rem, calc(0.91rem + 1.09vw), 1.75rem);
    --step-1: clamp(1.35rem, calc(1.06rem + 1.46vw), 2.19rem);
    --step-2: clamp(1.62rem, calc(1.23rem + 1.94vw), 2.73rem);
    --step-3: clamp(1.94rem, calc(1.43rem + 2.56vw), 3.42rem);
    --step-4: clamp(2.33rem, calc(1.66rem + 3.37vw), 4.27rem);
    --step-5: clamp(2.80rem, calc(1.92rem + 4.42vw), 5.34rem);
  
}


.project-main-container{
    height: auto;
    width: 100%;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    padding-bottom: 10em;

    padding-top: 5em;
    background-color:var(--black-color);
}
.project_wrapper{
    width: 80%;
    height: auto;
    color: var(--light-font);
}
.project_heading >h1{
    color: var(--light-font);
    font-size: 4rem;
    font-weight: 400;
    
}
.project_heading >p{
    color: var(--purple);
    font-size: var(--step--1);
    font-weight: 600;
    position: relative;
}
.project_heading >p::before{
    content: "";
    top: 50%;
    position: absolute;
    width: 17vw;
    background-color: var(--purple);
    height: 2px;
    left: 5em;

}

.project-main-div{
    width: 100%;
    height: auto;
    background-color: var(--black-color);
    display: flex;
    flex-direction: column;
    gap: 10em;
    padding-top: 5em;
    align-items: center;

}

/* project styling */
.project_1_container{
    width: 100%;
  

}
.project {
    padding: 2em;
    background-color: var(--black-color);
    width: 60em;
    height: 30em;
    box-shadow: 16px 14px 20px black;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;

    text-decoration: none;
}
.project:hover{
    cursor: pointer;
    background-color: rgb(22, 17, 24);
}

