.project_img_holder{
    width: 35em;
    height: 20em;
    overflow: hidden;
}
.project-1-img{
    background-image: url("../utils/img/project-1.png");
    width: 35em;
    height: 20em;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    filter: blur(2px);
    position: relative;
    transition: width 0.4s ease , height 0.4s ease , filter 0.4s ease;

}
@media (min-width: 1200px) {
    .project:hover .project-1-img{
        width: 36em;
        height: 21em;
        filter: blur(0px);
    }
}

.side-div-shadow-project-1{
    position: absolute;
    
    width: 30em;
    opacity: 0.3;
    filter: blur(150px);
    border-radius: 100%;
    
    height:  30em;
    background-color: var(--green);
    left: 700px;
    top: auto;
    right: auto;
    bottom:  1000px;
}
.project-details{
    width: 27em;
    height:auto;
    left: 30em;
    position: absolute;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2em;
    align-items: flex-end;
}

.project-number{
    font-size: 1.5em;
    width: 100%;
    position: relative;
    text-align: end;
    color: var(--light-font);
    
}


.project-name{
    display: flex;
    flex-direction: column;
    width: 100%;
    color: var(--light-font);
    align-items: flex-end;
}
.project-name h1{
  font-size: 2em;

}
.project-name p{
    font-size: 1em;
    color: var(--green);
}
.project-description{
    color: rgb(74, 214, 165);
    background-color: #331b31;
    width: 100%;
    box-shadow: 0px 0px 4px 2px #522b4e;
    padding: 1.3em;
    line-height: 1.3em;
   
    
}
.project-description span{
    font-size: 1.4em;
    
}
.project-description p{
    width: 100%;
    display: flex;
    justify-content: flex-end;
}
.project-stack{
    display: flex;
    width: 100%;
    justify-content: flex-end;
    gap: 1em;
    flex-wrap: wrap;
    color: var(--light-font);
}
.git{
    color: var(--green);
}

