*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: black;
    --light-font:  #EADFE8;

    --purple: #926A8E;

    --step--2: clamp(0.78rem, calc(0.66rem + 0.59vw), 1.12rem);
    --step--1: clamp(0.94rem, calc(0.78rem + 0.80vw), 1.40rem);
    --step-0: clamp(1.13rem, calc(0.91rem + 1.09vw), 1.75rem);
    --step-1: clamp(1.35rem, calc(1.06rem + 1.46vw), 2.19rem);
    --step-2: clamp(1.62rem, calc(1.23rem + 1.94vw), 2.73rem);
    --step-3: clamp(1.94rem, calc(1.43rem + 2.56vw), 3.42rem);
    --step-4: clamp(2.33rem, calc(1.66rem + 3.37vw), 4.27rem);
    --step-5: clamp(2.80rem, calc(1.92rem + 4.42vw), 5.34rem);
  
}
.Typewriter{
    
    color: var(--purple);
    display: inline;
}
.profile-main-container{
    width: 100%;
    
    height: 100vh;
    display: flex;
    justify-content: center;
    
}
.profile-wrapper{
    width: 80%;
   
    display: flex;
}
.profile-description{
    width: 60%;
    display: flex;
    z-index: 1;
    position: relative;
    flex-direction: column;
    justify-content: center;
    gap: 2em;
    transform: translateX(-130%);
   
}
.profile-description-animation{
    animation: profile-animation-1 1.3s ease forwards;
}

@keyframes profile-animation-1 {

    100%{
        transform: translateX(0%);
    }
    
}
.profile-description p{
    color: var(--purple);
    font-size: var(--step-0);
    position: relative;
    font-weight: 400;
}
.profile-description>div>div{
    display: flex;
    justify-content: flex-end;

}
.profile-description p::before{

    content: "";
    position: absolute;
    top: 50%;
    right: 5em;
    width: 17vw;
    height: 2px;
    background-color: #926A8E;

}
.profile-description h1{
    color: var(--light-font);
    font-size: var(--step-3);
    font-weight: 400;
}
.profile-image {
    overflow: hidden;
    width: 40%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    clip-path: polygon(47% 0, 100% 0, 100% 100%, 0% 100%);
    height: 100%;
    position: relative;
    background-image: url('../utils/img/profile-img-temp.jpg');
    
    
   
}

.profile-image::before{
    width: 100%;
    height: 100%;

    opacity: 1;
    content: "";
    position: absolute;
    transform: translateY(0%);
    background-color: var(--black-color);
    animation: profile-image-animation-1 1s ease forwards;
    animation-delay:0.8s;
}
@keyframes profile-image-animation-1 {
   
    0%{
        opacity: 1;
    }
  
    100%{
        opacity: 0;
    }
}
.profile-image::after{
    width: 100%;
    height: 100%;

    background-color: black;
    content: "";
    position: absolute;
    transform: translateY(100%);
    animation: profile-image-animation 2s ease forwards;
}
@keyframes profile-image-animation {
   
    0%{
        transform: translateY(100%);
    }
    50%{
        transform: translateY(0%);
    }
    100%{
        transform: translateY(-100%);
    }
}
.resume-btn{
    width: 17vw;
    font-size: var(--step--1);
    background-color:rgb(255, 255, 255 ,0);
    border: 1px solid white;
    box-shadow: none;
    color: white;
    font-weight: lighter;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding-top:0.4em;
    position: relative;
    padding-bottom:0.4em;
    overflow: hidden;
    transition: color 0.4s ease;
    
}
.resume-btn:hover{
    cursor: pointer;
    color: black;
  
}
.resume-btn:hover::before{
    left: 0;

    cursor: pointer;

}
.resume-btn::before{
    z-index: -1;
    width: 100%;
    height: 100%;
    position: absolute; 
    content: "";
    background-color: #ffffff;
    top: 0;
    left: -100%;
    transition: left 0.4s ease ;

}



.side-div-shadow{
    position: absolute;
    z-index: 1;
    width: 25vw;
    opacity: 0.3;
    filter: blur(120px);
    border-radius: 100%;
    
    height:  25vw;
    background-color: var(--purple);
    left: -200px;
    top: auto;
    right: auto;
    bottom:  0px;
}

@media (max-width: 768px) {
   
    .profile-image{
       display: none;
       width: 0%;
    }
    .profile-description{
        width: 100%;
    }
  }