*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: black;
    --light-font:  #EADFE8;
    --purple: #926A8E;
    --step--2: clamp(0.78rem, calc(0.66rem + 0.59vw), 1.12rem);
    --step--1: clamp(0.94rem, calc(0.78rem + 0.80vw), 1.40rem);
    --step-0: clamp(1.13rem, calc(0.91rem + 1.09vw), 1.75rem);
    --step-1: clamp(1.35rem, calc(1.06rem + 1.46vw), 2.19rem);
    --step-2: clamp(1.62rem, calc(1.23rem + 1.94vw), 2.73rem);
    --step-3: clamp(1.94rem, calc(1.43rem + 2.56vw), 3.42rem);
    --step-4: clamp(2.33rem, calc(1.66rem + 3.37vw), 4.27rem);
    --step-5: clamp(2.80rem, calc(1.92rem + 4.42vw), 5.34rem);
  

}
.side-div-shadow-contact{
    position: absolute;
    z-index: 1;
    width: 25vw;
    opacity: 0.3;
    filter: blur(120px);
    border-radius: 100%;
    
    height:  25vw;
    background-color: rgb(189, 107, 0);
    left: -200px;
    top: auto;
    right: auto;
    bottom:  100px;
}

.contact-main-container{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3em;
    padding-top: 5em;
    padding-bottom: 5em;
}

.contact-heading{
    color: #EADFE8;
    display: flex;
    width: 70%;
    align-items: flex-end;
    flex-direction: column;
    justify-content: flex-end;
}
.contact-heading h1{
    color: var(--light-font);
    font-size: var(--step-4);
    font-weight: 400;
}
.contact-heading >p{
    color: var(--purple);
    font-size: var(--step-0);
    font-weight: 600;
    position: relative;
}
.contact-heading >p::before{
    content: "";
    top: 50%;
    position: absolute;
    width: 17vw;
    background-color: var(--purple);
    height: 2px;
    right: 6em;

}

.contact-main-wrapper{
    width: 70%;
    position: relative;
    display: flex;
    
    justify-content: space-between;
    align-items: center;
    
}
