/* contact form */

.Contact_form{
    display: flex;
    justify-content: center;
    gap: 2em;
    color: var(--light-font);
    flex-direction: column;
    width: 50%;
    height: 100%;
}
.Contact_form div{
    display: flex;
    justify-content: center;

    flex-direction: column;
}
.Contact_form input{
    border: none;
    background-color: rgba(255, 255, 255, 0);
    border-bottom: 1px rgb(255, 255, 255) solid;
    padding: 1em 0px 0em 0px;
    font-size: var(--step--1);
    color: var(--light-font);
}   

.Contact_form input:focus {
    outline: none;
  }
.Contact_form label{
    font-size: var(--step--1);
    
}
.Contact_form h1{
    font-size: var(--step-3);
    color: var(--purple);
}
.Contact_form form {
    display: flex;
    justify-content: center;
 
    flex-direction: column;
    width: 100%;
    height: 100%;
    gap: 2em;

}
.Contact_form h1 span{
    color: var(--light-font);
    font-size: var(--step-3);
    font-weight: 400;
}
.Contact_form button{
    padding: 0.5em;
    font-size: var(--step--1);
    border: 1px rgb(255, 255, 255) solid;
    position: relative;
    background-color: rgba(255, 255, 255, 0);
    overflow: hidden;
    transition: color 0.5s ease;
    color: rgb(255, 255, 255);
    z-index: 1;
}
.Contact_form button:hover{
    cursor: pointer;
    color: rgb(0, 0, 0);
}
.Contact_form button:hover::before{
    left: 0%;
}
.Contact_form button::before{
    content: "";
    position: absolute;
    width: 100%;
    top: 0;
    left: -100%;
    transition: left 0.5s ease;
    height: 100%;
    background-color: rgb(255, 255, 255);
    z-index: -1;
}


/* contact img  */

.contact-img-div{
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact-img{
    background-image: url("../utils/img/contact-img.jpg");
    background-position: -100px;
    background-repeat: no-repeat;
    background-size: cover;
    width: 35em;
    height: 35em;
    position: relative;
}
.contact-img::before{
    background-color: rgb(20, 15, 22,0.7);
    width: 100%;
    height: 100%;
    content: "";
    position: absolute;

}
@media (max-width: 700px){
    .contact-img{
        width: 0%;
    }
    .Contact_form{
        width: 100%;
    }
    .contact-img-div{
        width: 0%;
    }
}