*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: black;
    --light-font:  #EADFE8;
    --purple: #926A8E;
    --step--2: clamp(0.78rem, calc(0.66rem + 0.59vw), 1.12rem);
    --step--1: clamp(0.94rem, calc(0.78rem + 0.80vw), 1.40rem);
    --step-0: clamp(1.13rem, calc(0.91rem + 1.09vw), 1.75rem);
    --step-1: clamp(1.35rem, calc(1.06rem + 1.46vw), 2.19rem);
    --step-2: clamp(1.62rem, calc(1.23rem + 1.94vw), 2.73rem);
    --step-3: clamp(1.94rem, calc(1.43rem + 2.56vw), 3.42rem);
    --step-4: clamp(2.33rem, calc(1.66rem + 3.37vw), 4.27rem);
    --step-5: clamp(2.80rem, calc(1.92rem + 4.42vw), 5.34rem);
  
}


.main-menu-container{
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0,0);
}

.menu-wrapper{
    width: 65%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
@media (max-width: 1476px){
    .menu-wrapper{
        width: 80%;
    }

}

.menu-links-div {
    position: relative;
    z-index: 1;
    font-size: var(--step-5);
    overflow: hidden;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
   
    color: rgb(0, 0, 0);
    height: 100%;
}
@media (max-width: 1000px){
    .menu-wrapper{
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
    .menu-links-div{
        width: 80%;
        height: 60%;
    }

}
.menu-links-div>div{
    display: flex;
    z-index: 1;
    position: relative;
    align-items: center;
}
.menu-links:hover{
    cursor: pointer;
}

.menu-left-line:has(+ .menu-links:hover) {
    width: 20%;
}
.menu-left-line{
    transition: width 0.6s ease;
    width: 0%;
    height: 6px;
    background-color: black;
    
    


}
.menu-links{
    position: relative;
    left: 0;
}
.menu-div{
    border-right: 1px #d0d0d0 solid;
    background-color: var(--light-font);
}
.menu-div-1{
    position: absolute;
    left: 0;
    width: 20%;
    height: 0%;
    bottom: 0;

    background-color: var(--light-font);

}
.menu-contact-info{
    position: relative;
    overflow: hidden;
    z-index: 1;
    
}
.menu-contact-info div{
    left: 0;
    position: relative;

}
.menu-contact-info h1{
    font-size: var(--step-1);
}
.menu-contact-info p{
    font-size:var(--step--1);;
}

.menu-div-2{
    position: absolute;
    left: 20%;
    width: 20%;
    bottom: 0;
    height: 100%;
    background-color: var(--light-font);
}
.menu-div-3{
    position: absolute;
    left: 40%;
    width: 20%;
    bottom: 0;
    height: 100%;
    background-color: var(--light-font);
}
.menu-div-4{
    position: absolute;
    left: 60%;
    width: 20%;
    bottom: 0;
    height: 100%;
    background-color: var(--light-font);
}
.menu-div-5{
    position: absolute;
    left: 80%;
    width: 20%;
    bottom: 0;
    height: 100%;
    background-color: var(--light-font);
}