*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
:root{
    --black-color: black;
    --light-font:  #EADFE8;
    --grey-font:   #a6a6a6;
    --purple: #926A8E;
 
    --step--2: clamp(0.78rem, calc(0.66rem + 0.59vw), 1.12rem);
    --step--1: clamp(0.94rem, calc(0.78rem + 0.80vw), 1.40rem);
    --step-0: clamp(1.13rem, calc(0.91rem + 1.09vw), 1.75rem);
    --step-1: clamp(1.35rem, calc(1.06rem + 1.46vw), 2.19rem);
    --step-2: clamp(1.62rem, calc(1.23rem + 1.94vw), 2.73rem);
    --step-3: clamp(1.94rem, calc(1.43rem + 2.56vw), 3.42rem);
    --step-4: clamp(2.33rem, calc(1.66rem + 3.37vw), 4.27rem);
    --step-5: clamp(2.80rem, calc(1.92rem + 4.42vw), 5.34rem);
  
}

.navbar-main-container{
    width: 100%;
    position: fixed;
    z-index: 2;
    display: flex;
    top: -100%;
    justify-content: center;
    align-items: center;
}
.navbar-main-container_animation{
    animation: nav_animation 0.7s ease forwards;
}
@keyframes nav_animation {
    100%{
        top: 0%;
    }
}
.navbar-wrapper{
    width: 90%;

}
.logo{
    
    font-size: 1.3rem;
    padding: 1.4rem;
}
.navbar{
    list-style: none;
    display: flex;
    justify-content: space-between;
    width: 100%;
    color: var(--light-font);
    align-items: center;
    transition: color 1s ease;
}
.black-color{
    color: var(--black-color);
}
.nav-links{
    display: flex;
    
    width: 40%;
}



.navbar>.nav-links>div>a:hover{
    color: var(--grey-font);
    cursor: pointer;
}
.nav-links a{
    text-decoration: none;
    color: var(--light-font);
}
.navbar >.nav-links>div{
    padding: 1.4rem;
   

    font-size: 1.3rem;
}
.hamburger-menu{
    
    width: 40px;
    height: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.hamburger-menu:hover{
    cursor: pointer;
}

.hamburger-menu-bars{
    width: 100%;
    background-color: white;

    height: 6%;

    transition: transform 0.5s ease ,   background-color 1s ease;
}

.crossed-menu1{
    background-color: black;
    transform: rotate(45deg) translateY(10px);
    
}
.crossed-menu2{
    background-color: black;
    transform: rotate(-45deg)  translateY(-10px);
}

@media (max-width: 768px) {
   
    .nav-links{
       display: none;
    }
    
  }