*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --black-color: rgb(20, 15, 22);
    --light-back:rgb(183, 161, 191);
    --light-font:  #EADFE8;
    --purple: #926A8E;
    --light-orange:rgb(255, 223, 143);
    --green:rgb(74, 214, 165);
    --step--2: clamp(0.78rem, calc(0.66rem + 0.59vw), 1.12rem);
    --step--1: clamp(0.94rem, calc(0.78rem + 0.80vw), 1.40rem);
    --step-0: clamp(1.13rem, calc(0.91rem + 1.09vw), 1.75rem);
    --step-1: clamp(1.35rem, calc(1.06rem + 1.46vw), 2.19rem);
    --step-2: clamp(1.62rem, calc(1.23rem + 1.94vw), 2.73rem);
    --step-3: clamp(1.94rem, calc(1.43rem + 2.56vw), 3.42rem);
    --step-4: clamp(2.33rem, calc(1.66rem + 3.37vw), 4.27rem);
    --step-5: clamp(2.80rem, calc(1.92rem + 4.42vw), 5.34rem);
    --light-orange:rgb(255, 223, 143);
    --green:rgb(74, 214, 165);
}

.about_main_container{
    height: auto;
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 10em;
    padding-bottom: 15em;
}
.about_wrapper{
    height: 100%;
    width: 80%;
}
.about_me_details_section{
    
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
    padding: 1em;
    
    color: #EADFE8;
}
.about_me_mainSection{
    display: flex;
    gap: 5em;
    flex-direction: row;
}

.about_me_details_section span{
    color: var(--green);
    font-weight: 500;
}
.about_me_details_section p{
    color: var(--light-back);
    font-size: 1.04em;
}
.my_image_section{
    position: relative;
   /* display: flex;
   justify-content: center;
   align-items: center; */

    width: 100%;
    height: 100%;
}
.my_image_holder{
    width: 20em;
    height: 20em;
    overflow: hidden;
    box-shadow: 16px 14px 20px black;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
   
    
    left: 28%;
   
}

.my_image_holder::before{
    content: "";
    background-image: conic-gradient(
        rgb(74, 214, 165) 20deg,
        transparent 120deg
    );
    width: 150%;
    height: 150%;
    position: absolute;
    animation: rotate 2s linear infinite;
}

@keyframes rotate {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
    
}
.my_image_holder::after{
    content: "";
    background-color:var(--black-color);
    width: 97%;
    height: 97%;
    position: absolute;
    z-index: 0;
}

.my_image{
    z-index: 1;
    background-image: url("../utils/img/Mypic.jpg");
    background-position: -50px -50px;
    background-repeat: no-repeat;
    background-size: cover;
    width: 23em;
    height: 20em;
    top: -8%;

    left: 24%;
    
    position: absolute;
}
@media (max-width:1300px) {
    .my_image{
        left: 18%;
    }
    .my_image_holder{
        left: 24%;
    }
}
@media (max-width:1158px) {
    .my_image{
        left: 5%;
    }
    .my_image_holder{
        left: 11%;
    }
}
@media (max-width:1000px) {
    .my_image{
        left: 0%;
    }
    .my_image_holder{
        left: 5%;
    }
}
@media (max-width:400px){
    .my_image_holder{
        width: 16em;
        height: 16em;
    }
}
.about_me_heading{
    width: 100%;
    color: var(--light-font);
    font-size: var(--step-1);
    padding-left: 0.5em;
    
}
.about_me_heading h1{
    font-weight: 400;
}
.tech_stack{
    width: 100%;
    padding: 1em;
}
.tech_stack>div{
    display: flex;
    justify-content: flex-start;
    gap: 3em;
}
.tech_stack>div>ul{
    color: var(--green);
    font-weight: 500;
}


.side-div-shadow-aboutme{
    position: absolute;
    
    width: 30em;
    opacity: 0.3;
    filter: blur(140px);
    border-radius: 100%;
    
    height:  30em;
    background-color: var(--green);
    right: 300px;
    top: auto;
    left: auto;
    bottom:  0px;
}

@media (max-width : 800px){
    .about_me_details_section{
        width: 100%;
        
    }
    .about_me_mainSection{
        flex-direction: column;
    }
    .my_image_section{
        width: 100%;
        
    }
    .my_image{
        left: 20%;
    }
    .my_image_holder{
        left: 24%;
    }
  
    
}
@media (max-width: 600px){
    .about_wrapper{
        width: 100%;
    }
}
@media (max-width: 550px){
    .my_image{
        left: 5%;
        width: 21em;
    height: 18em;
    }
    .my_image_holder{
        left: 10%;
        width: 18em;
    height: 18em;
    }
}